import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const list = r => require.ensure([], () => r(require('@/pages/list')), 'list')
const log = r => require.ensure([], () => r(require('@/pages/log')), 'log')
const power = r => require.ensure([], () => r(require('@/pages/power')), 'power')
const role = r => require.ensure([], () => r(require('@/pages/role')), 'role')
const error = r => require.ensure([], () => r(require('@/pages/error')), 'error')
const routes = [
  {
    path: '/',
    component: list
  },
  {
    path: '/log',
    component: log
  },
  {
    path: '/error',
    component: error
  },
  {
    path: '/power',
    component: power
  },
  {
    path: '/role',
    component: role
  }
]

export default new Router({
  routes,
  strict: process.env.NODE_ENV !== 'production'
})



